<template>
  <div>
    <template v-if="connection">
      <b-button
        variant="transparent"
        :class="`text-center text-white rounded-0 d-flex flex-column align-items-center h-100 keyboard-number justify-content-center ${disableVoicemailButton ? 'voxdesk-btn-disabled' : ''}`"
        v-b-modal.showVoicemails
        block
        :disabled="voice_mail || disableVoicemailButton"
      >
        <span class="fe fe-voicemail" />
        <small>Voicemail</small>
      </b-button>
    </template>
    <b-modal centered size="sm" id="showVoicemails" body-class="rounded-sm p-3" hide-footer hide-header>
      <!-- Dropdown starts here -->
      <b-dropdown id="voicemail_list" :text="file" variant="white" size="sm" block toggle-class="d-flex justify-content-between align-items-center" menu-class="vh-97 overflow-auto py-2">
        <b-dropdown-item v-for="(item, index) in media" :key="index" @click="sendVoicemail(item.key), (file = item.key.split('/')[2])">
          <p class="mb-0 text-truncate">{{ get_dropdown_text(item) }}</p>
        </b-dropdown-item>
      </b-dropdown>
      <b-overlay :show="isBusy" spinner-variant="primary" spinner-small no-wrap opacity="0.5" />
      <span :class="`text-${style}`" class="small">{{ message }}</span>
    </b-modal>
  </div>
</template>

<script>
  import call from "@/js/dialer/call";
  import Public from "@/js/core/public";

  export default {
    props: {
      connection: Object,
      disableVoicemailButton: Boolean,
    },
    data() {
      return {
        media: [],
        voice_mail: false,
        file: "Select Voicemail",
        isBusy: false,
        message: "",
        style: "success",
      };
    },
    async created() {
      this.media = await call.getMedia();
    },
    methods: {
      async sendVoicemail(audio) {
        console.log("connection", this.connection);
        this.isBusy = true;
        // Prepare s3 url
        let audio_url = await Public.get_public_s3url(audio);
        try {
          await call.sendVoicemail(this.connection.parameters.CallSid, audio_url);
          this.message = "Voicemail dropped";
          this.$bvModal.hide("showVoicemails");
        } catch (e) {
          console.log("error", e);
          window.e = e;
          this.message = "Unable to drop voicemail";
          this.style = "danger";
        }
        this.isBusy = false;
      },
      get_dropdown_text(data) {
        let temp_name = data.key.split("/");
        if (temp_name.length == 4) return temp_name[3];

        return temp_name[2];
      },
    },
  };
</script>
<style>
  .vh-97 {
    height: 97vh !important;
    width: 100%;
    margin: auto !important;
  }
  .vh-97 .dropdown-item:hover {
    background-color: #edf2f9 !important;
  }
</style>
