import Data from "@/js/core/data";

class Public extends Data {
  get_public_s3url(relative_path) {
    let region = super.getRegion();
    let prepend_region = region == "dev" || region == "qa" ? `ap1-${region}` : region;
    return `https://${prepend_region}-infinity-user-data.s3.amazonaws.com/${relative_path}`;
  }
}

export default new Public();
